            </view>
            
            <view class="topContent">
            	<image v-if="head_image.length == 0" class="headerImage" src="../../static/default_headImage@2x.png" @tap="ChooseImage" mode="aspectFill">
            	</image>
                <image v-else class="headerImage" :src="head_image" mode="aspectFill" @tap="ChooseImage">
                </image>
            	<view class="telephone">
                    <view>
            		未登录
                    </view>
            	</view>
            </view>
