                <view :class="TabCur == '0'? 'btn1' : 'btn2'" @click="btn1pressed">
                    {{tabNav[0]}}
                </view>
                <view :class="TabCur == '1'? 'btn1' : 'btn2'" @click="btn2pressed">
                    {{tabNav[1]}}
                </view>
            </view>
		</view>
        
        <view v-if="data.length == 0" class="empty">
            <image src="/static/homeBg.png" class="emptyImg"></image>
            <view class="emptyLabel">
                您尚未登录<br>登录后可查看您的环卫事件<br>
            </view>
            
            <button class="cu-btn shadow bg-gradual-blue lg round loginBtn" @click="gotoLogin">立即登录</button>
        </view>
