<script>
    import Vue from 'vue'
    import {
    	mapState
    } from 'vuex'
	export default {
        computed: {
            ...mapState(['token']),
        },
        onShow() {
            if (this.token == "" || typeof(this.token) == "undefined") {
               
            } else {
                uni.reLaunch({
                    url:'../index/workIndex'
                });
            }
        },
        
        
		data() {
			return {
				data: [],
				modalName: null,
				balanceBonus:"0.00",
				totalBonus:"0.00",
				phone:"欢迎您，老弟",
				loadModal:false,
				isLoad:false,
				subcard_num: 0,
				assignGas:false,
				longitude:0,
				latitude:0,
				unread_message:0,
				head_image:'',
				share_image:'../../static/group3.png',
                current: 0,
                tabIndex: 0,
                TabCur: 0,
                cardNo:'',
                tabNav: ['待处理', '全部事件'],
                loadModal:false,
                showLoadMoreALL: false,
                pageNoAll:1,
                data: [],
                isLoadAll:false,
                loadMoreTextAll: "加载中...",
			}
		},
		methods: {
            ChooseImage() {
                if (this.phone == "" || this.token == "" || typeof(this.token) == "undefined") {
                    uni.showModal({
                        title: '提示',
                        content: '您当前未登录，登录后可享受更好的服务',
                        confirmText: '去登录',
                        success: function (res) {
                            if (res.confirm) {
                                console.log('用户点击确定');
                                uni.reLaunch({
                                    url:"../launchPage/launchPage?unlogin="+"unlogin"
                                });
                            } else if (res.cancel) {
                                console.log('用户点击取消');
                            }
                        }
                    });
                } 
            },
            gotoLogin() {
              uni.reLaunch({
                  url:"../launchPage/launchPage?unlogin="+"unlogin"
              });  
            },
            uploadImage(imageUrl) {
                this.LoadModal()
                uni.uploadFile({
                    url: this.GLOBAL.UploadImage_URL, //仅为示例，非真实的接口地址
                    name:'file',
                    filePath:imageUrl,
                    formData: {
                        'file': 'file',
                        'user': 'test'
                    },
                    success: (uploadFileRes) => {
                        uni.hideLoading();
                        console.log(uploadFileRes.data);
                        let response = JSON.parse(uploadFileRes.data);
                        if (response.code == 200) {
                            this.upload(response.data)
                        } else {
                            uni.showToast({
                            	title:response.message,
                            	icon:"none"
                            }) 
                        }
                    },
                    fail: (err) => {  
                        uni.hideLoading();
                            console.log(err)  
                        },  
                        complete: (complete) => {  
                            console.log(complete)   
                        }  
                });
            },
            
            upload(imageUrl) {
                this.LoadModal()
                uni.request({
                    url: this.GLOBAL.UploadHeaderImage,
                    dataType: 'jsonp',
                    method: 'POST',
                    data: {
                        access_token:this.token,
                        head_image:imageUrl,
                    }
                }).then(data => {
                    //data为一个数组，数组第一项为错误信息，第二项为返回数据
                    uni.hideLoading();
                    var [error, res]  = data;
                    console.log(res.data);
                    let response = JSON.parse(res.data);
            
                    if ( response.code == 200 ) {
                        uni.showToast({
                        	title:"头像上传成功",
                        	icon:"none"
                        })
                        this.head_image = imageUrl
                    } else {
                        uni.showToast({
                        	title:response.message,
                        	icon:"none"
                        }) 
                    }
                }).catch(err => {
                    uni.hideLoading();
                	console.log('request fail', err);
                	uni.showModal({
                		content: err.errMsg,
                		showCancel: false
                	});
                }); 
            },
		}
	}
</script>

